/* Home.css */
.lights {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, #E0E0E0, #b8acd8, #9973e5);
  mix-blend-mode: screen;
  pointer-events: none;
  z-index: -1;
  animation: lightsAnimation 5s infinite;
}

@keyframes lightsAnimation {
  0%, 100% {
    opacity: 0.8;
  }
  50% {
    opacity: 0.2;
  }
}
.overlay {
  position: absolute;
  top: 50%; /* Adjust to position vertically as desired */
  left: 50%; /* Adjust to position horizontally as desired */
  transform: translate(-50%, -50%);
  width: 50%;
  max-width: 80%; /* Adjust to control the maximum width */
  height: auto; /* Maintain aspect ratio */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}